body {
  font-family: 'Roboto', sans-serif;
}
.dark-skin {
  background-color: #1b1d23;
  color: #c2c4cc;
}
.light-skin {
  background-color: #edf1f5;
  color: #575962;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}
.dark-skin a {
  color: #ffffff;
}
.light-skin a {
  color: #f57e22;
}
a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.navbar {
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    background-color: #15161b;
    position: absolute;
    width: 100%;
    left: 0;
    top:0;
}
.dark-skin .navbar {
    background-color: #15161b;
}
.light-skin .navbar {
    background-color: #ffffff;
}

.navbar-btn {
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none !important;
    border: none;
    margin-top: -76px;
}
.dark-skin .navbar-btn {
  color: #c2c4cc;
  background-color: #000 !important;
}
.light-skin .navbar-btn {
  color: #fff;
  background-color: #000 !important;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: stretch;
        align-items: stretch;
}
#sidebar {
  min-width: 250px;
  max-width: 250px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.dark-skin #sidebar {
    color: #fff;
}

.light-skin #sidebar {
    background-color: #fff;
    -webkit-box-shadow: 3px 0px 20px -3px rgba(0, 0, 0, 0.14);
    box-shadow: 3px 0px 20px -3px rgba(0, 0, 0, 0.14);
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebar-header {
    padding: 20px;
}

#sidebar ul.components {
    padding: 20px 0;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}
#sidebar ul li {
  width: 80%;
  margin: 0 auto;
  line-height: 30px;
}
.dark-skin #sidebar + ul li a {
  border-left: 5px solid;
  line-height: 30px;
}
.light-skin #sidebar ul li a {
  border-radius: 4px;
}
#sidebar ul li a {
    display: block;
    font-weight: lighter;
}
.dark-skin #sidebar ul li a {
  color: #c2c4cc;
  padding: 10px;
  font-size: 0.9em;
}
.dark-skin #sidebar ul li a i {
  display: none;
}
.light-skin #sidebar ul li a {
  color: #54667a;
  padding: 3px 10px;
  font-size: 0.85em;
}
#sidebar ul li a:hover {
    text-decoration: none;
    background-color: transparent;
}
.dark-skin #sidebar ul li a:hover {
    color: #ffffff;
    padding-left: 5px;
    border-left: 5px solid #168bb6;
}
.light-skin #sidebar ul li a:hover {
    color: #000000 !important;
}



.dark-skin #sidebar ul li a.active {
    border-left: 5px solid #f57e22;
}
#sidebar ul li.sub-active a {
    font-weight: bold;
}
.light-skin #sidebar ul li a.active {
    background-color: #1997c6;
}
.light-skin #sidebar ul li a.active {
  color: #ffffff;
}
.navbar-header {
  width: 100%;
}


a[data-toggle="collapse"] {
    position: relative;
}

a[aria-expanded="false"]::before, a[aria-expanded="true"]::before {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
    position: absolute;
    right: 20px;
    top: 20px;
}
a[aria-expanded="true"]::before {
    display: none;
}


ul ul a {
    font-size: 0.8em !important;
    padding-left: 20px !important;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

#sidebar .sidebar-user  {
  width: 50px;
  height: 50px;
  margin: 0 auto;
  padding-top: 3px;
}

#sidebar .sidebar-user small {
  margin-top: -4px;
  display: block;
}

.light-skin .sidebar-user  {
  background-color: #1997c6;
  color: #ffffff;
}
.dark-skin .sidebar-user  {
  background-color: #000000;
}
/* ---------------------------------------------------
    COLORS STYLE
----------------------------------------------------- */

.color-orange {
  color: #f57e22;
}
.has-error label {
    color: #a60017 !important;
}
.light-skin .color-title {
    color: #1997c6;
}
.light-skin .color-title {
    color: #1997c6;
}
/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
    min-height: 100vh;
    width: 100%;
    padding: 40px;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

#page-content {
  margin-top: -20px;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {
    #sidebar {
        margin-top: 130px;
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #sidebarCollapse span {
        display: none;
    }
    #page-content {
      margin-top: 100px;
    }
    #sidebar .hm-logo {
      display: none;
    }
}


