input[type="checkbox"] {
  margin-top: 9px;
}
/* ---------------------------------------------------
    SPINER
----------------------------------------------------- */
.lds-dual-ring {
    display: inline-block;
    width: 25px;
    height: 25px;
    margin-right: 10px;
    margin-top: -5px;
}

.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 25px;
    height: 25px;
    margin: 1px;
    border-radius: 50%;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
}
.btn .lds-dual-ring,.small.lds-dual-ring {
    width: 15px;
    height: 15px;
    margin-right: 0px;
}
.btn .lds-dual-ring:after,.small.lds-dual-ring::after {
    width: 15px;
    height: 15px;
}

.dark-skin .lds-dual-ring:after {
    border: 5px solid #fff;
    border-color: #fff transparent #fff transparent;
}
.light-skin .lds-dual-ring:after {
    border: 5px solid #575962;
    border-color: #575962 transparent #575962 transparent;
}
.dark-skin .btn .lds-dual-ring:after {
    border: 2px solid #fff;
    border-color: #fff transparent #fff transparent;
}
.light-skin .btn .lds-dual-ring:after {
    border: 2px solid #575962;
    border-color: #575962 transparent #575962 transparent;
}
@-webkit-keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
/* ---------------------------------------------------
    LOGIN
----------------------------------------------------- */
.login-card {
  width: 18rem;
}
/* ---------------------------------------------------
    SWITCH
----------------------------------------------------- */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {display:none;}


.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  -webkit-box-shadow: 0 0 1px #2196F3;
          box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
